.ifs-timeline {
  position: relative;
  height: inherit;
  width: inherit; }
  .ifs-timeline .ifs-bar {
    fill: #437d96; }
  .ifs-timeline .ifs-y-axis {
    font-family: 'LatoLatinWeb', Arial, Helvetica, sans-serif; }
  .ifs-timeline .ifs-timeline-breadcrumb {
    position: absolute;
    top: 0;
    left: 0;
    display: inline-flex;
    margin: 0;
    padding: 0;
    list-style-type: none;
    font-family: 'LatoLatinWeb', Arial, Helvetica, sans-serif; }
  .ifs-timeline .ifs-timeline-breadcrumb li a {
    background: #d4d4d4;
    color: #333;
    display: block;
    font-size: 14px;
    height: 24px;
    line-height: 24px;
    margin-right: 23px;
    padding: 0 10px 0 5px;
    position: relative;
    text-decoration: none; }
  .ifs-timeline .ifs-timeline-breadcrumb li:first-child a {
    padding-left: 15px; }
  .ifs-timeline .ifs-timeline-breadcrumb li:first-child a:before {
    border: none; }
  .ifs-timeline .ifs-timeline-breadcrumb li a:before,
  .ifs-timeline .ifs-timeline-breadcrumb li a:after {
    content: "";
    position: absolute;
    top: 0;
    border: 0 solid #d4d4d4;
    border-width: 12px 10px;
    width: 0;
    height: 0; }
  .ifs-timeline .ifs-timeline-breadcrumb li a:before {
    left: -20px;
    border-left-color: transparent; }
  .ifs-timeline .ifs-timeline-breadcrumb li a:after {
    left: 100%;
    border-color: transparent;
    border-left-color: #d4d4d4; }
  .ifs-timeline .ifs-timeline-breadcrumb li a:hover {
    background-color: #169cd7;
    color: white; }
  .ifs-timeline .ifs-timeline-breadcrumb li a:hover:before {
    border-color: #169cd7;
    border-left-color: transparent; }
  .ifs-timeline .ifs-timeline-breadcrumb li a:hover:after {
    border-left-color: #169cd7; }
  .ifs-timeline .ifs-timeline-breadcrumb li:last-child a {
    background-color: #169cd7;
    color: white; }
  .ifs-timeline .ifs-timeline-breadcrumb li:last-child a:before {
    border-color: #169cd7;
    border-left-color: transparent; }
  .ifs-timeline .ifs-timeline-breadcrumb li:last-child a:after {
    border-left-color: #169cd7; }

.ifs-chart-tooltip {
  background: rgba(0, 0, 0, 0.8);
  border-radius: 2px;
  color: white;
  font-size: 12px;
  padding: 6px;
  text-align: center;
  transform: translateY(-15px);
  font-family: 'LatoLatinWeb', Arial, Helvetica, sans-serif; }
  .ifs-chart-tooltip:after {
    bottom: -11px;
    color: rgba(0, 0, 0, 0.8);
    content: "\25BC";
    font-size: 13px;
    position: absolute;
    right: -1px;
    width: 100%; }
